import { Box } from '@mui/material'
import React from 'react'
import { useEffect } from 'react';

const changeColor = () => {
    var randomHex = (Math.random() * 0xFFFFFF << 0).toString(16).padStart(6, '0');
    fetch("https://www.thecolorapi.com/id?hex=" + randomHex)
    .then(res => res.json())
    .then(data => {
        document.getElementById("color-box").style.background = `${data.hex.value}`;
        document.getElementById("color-name").textContent = `${data.name.value}`;
        document.getElementById("color-cmyk").innerHTML = `CMYK: (${data.cmyk.c},${data.cmyk.m},${data.cmyk.y},${data.cmyk.k})`;
        document.getElementById("color-hex").textContent = `HEX: ${data.hex.value}`;
        document.getElementById("color-rgb").textContent = `RGB: (${data.rgb.r},${data.rgb.g},${data.rgb.b})`;
    });
}



export default function ColorPicker() {

    useEffect(() => {
        changeColor();
    }, []);

  return (

    <Box bgcolor={'background.default'} color={'text.primary'}>
            <div id="color-project">
                <div id="color-container">
                    <div id="color-box" style={{backgroundColor: '#ffffff'}}>
                    </div>
                    <div className="color-info">
                        <div>
                        <h2 id="color-name"></h2>
                        <p id="color-cmyk"></p>
                        <p id="color-hex"></p>
                        <p id="color-rgb"></p>
                        </div>
                        <button id="color-button" onClick={changeColor}>Generate New Color</button>
                    </div>
                </div>
            </div>
    </Box>
    
  )
}
